.Input {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.Input label {
  display: block;
  width: 80%;
  font-weight: 600;
  margin: 4px 0;
}

.Input input {
  display: block;
  width: 80%;
  margin: 4px 0;
  outline: none;
}

.Input textarea {
  display: block;
  margin: 4px 0;
  width: 80%;
  padding: 1rem;
}



