.Navbar {
  height: 3rem;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgb(35, 146, 141);
  width: 100%;
  margin-bottom: 3rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  padding: 0 2rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: rgb(232, 247, 239);
  z-index: 200;
}

.Navbar Link {
  text-decoration: none;
  color: black;
}



