.Hr {
  width: 100%;
  border: 0;
  height: 1px;
  background: rgb(20, 20, 20);
  background-image: -webkit-gradient(
    linear,
    left top, right top,
    from(rgb(163, 159, 159)),
    color-stop(rgb(20, 20, 20)),
    to(rgb(163, 159, 159))
  );
  background-image: -webkit-linear-gradient(
    left,
    rgb(163, 159, 159),
    rgb(20, 20, 20),
    rgb(163, 159, 159)
  );
  background-image: -o-linear-gradient(
    left,
    rgb(163, 159, 159),
    rgb(20, 20, 20),
    rgb(163, 159, 159)
  );
  background-image: linear-gradient(
    to right,
    rgb(163, 159, 159),
    rgb(20, 20, 20),
    rgb(163, 159, 159)
  );
}



