.Card {
  width: 85%;
  border: 1px solid black;
  margin: 2.5rem 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 2rem;
  -webkit-box-shadow: 5px 5px 8px #b1afaf;
          box-shadow: 5px 5px 8px #b1afaf;
  /* box-shadow: 5px 5px 8px var(--LightGreen); */
  min-height: 6.3rem;
}

.Frag {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.Buttons {
  margin-left: 1.7rem;
}

.Card pre {
  background-color: var(--LightGreen);
  padding: 1rem;
}

.Card img {
  width: 70%;
  display: block;
  border: 1px solid black;
  /* border-radius: 0.1rem; */
  padding: 0.5rem;
}

.Card a {
  color: var(--Blue);
}



